/* ======== RESET Y BASE ======== */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  color: #333;
}

/* ======== LAYOUT GENERAL ======== */
.app-container {
  display: flex;
  min-height: 100vh;
  flex-direction: row;
}

/* ======== SIDEBAR ======== */
.sidebar {
  width: 220px;
  background-color: #263238;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.sidebar a {
  color: white;
  text-decoration: none;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background-color 0.3s;
}

.sidebar a:hover,
.sidebar a.active {
  background-color: #37474f;
}

/* ======== CONTENIDO PRINCIPAL ======== */
.content {
  flex: 1;
  padding: 30px;
  overflow-y: auto;
}

.content h2 {
  margin-bottom: 20px;
  font-size: 1.8rem;
}

/* ======== FILA PARA GRÁFICA + FILTRO ======== */
.historico-fila {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
}

/* ======== FILTRO DE TIEMPO ======== */
.filtro-tiempo {
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  min-width: 200px;
  max-width: 250px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.filtro-tiempo label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.95rem;
}

.filtro-tiempo input[type="radio"] {
  accent-color: #ff5252;
}

/* ======== CONTENEDOR DE GRÁFICA ======== */
.chart-container {
  position: relative;
  width: 100%;
  min-width: 800px;
  min-height: 400px;
}
.chart-container canvas {
  width: 100%;
  height: 100%;
}


/* ======== RESPONSIVE DESIGN ======== */
@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
  }

  .historico-fila {
    flex-direction: column;
    align-items: center;
  }

  .filtro-tiempo {
    width: 90%;
  }

  .chart-container {
    height: 300px;
  }
}

@media (max-width: 480px) {
  .chart-container {
    height: 250px;
  }

  .sidebar a {
    font-size: 0.9rem;
    padding: 10px;
  }

  .filtro-tiempo label {
    font-size: 0.85rem;
  }
}
